<template>
  <div class="start-page">
    <div class="header">
        <button class="search-btn" @click="openModal">Поиск</button>
    </div>
    <div class="container">
        <div v-if="pages > 1">
            <div class="pages">
                <div class="pages-item" v-for="page in pages" :key="page" @click="setPage(page)">{{page}}</div>
            </div>
        </div>
        <div v-if="!items.length" class="no-posts">
            <div class="no-posts">
                Здесь пока нет записей
            </div>
            <button class="search-btn" @click="openModal" style="margin: auto;margin-top: 20px;">Поиск</button>
        </div>
        <div class="posts" v-else>
            <div v-if="table == 'history'">
                <div class="posts-header">
                    <div class="posts-header-item" v-for="field in historyFields" :key="field">
                        {{field}}
                    </div>
                </div>
                <div class="posts-content">
                    <div class="posts-content-item" v-for="item in items" :key="item.id">
                        <div class="posts-content-item-attr" v-for="f in historyFields" :key="f">
                            {{item[f]}}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div v-if="isOpen" class="modal">
        <div class="modal-container">
            <div class="modal-header">
                <div class="modal-header-title">Поиск</div>
                <button class="modal-header-close" @click="isOpen = false">X</button>
            </div>
            <div v-if="!isSearch" class="modal-content">
                <div class="modal-content-row">
                    <select v-model="table">
                        <option value="history">
                            history
                        </option>
                        <option value="proxy">
                            proxy
                        </option>
                    </select>
                    <label>Таблица</label>
                </div>
                <div class="modal-content-row">
                    <select v-model="sort" v-if="table == 'history'">
                        <option v-for="item in historyFields" :key="item" :value="item">
                            {{item}}
                        </option>
                    </select>
                    <select v-model="sort" v-if="table == 'proxy'">
                        <option v-for="item in proxyFields" :key="item" :value="item">
                            {{item}}
                        </option>
                    </select>
                    <label>Сортировка</label>
                    <input v-model="reverseSort" type="checkbox"/>
                    <label>Обратная</label>
                </div>
                <div class="modal-content-row">
                    <input v-model="param1" placeholder="Param 1" type="text">
                    <label>Число</label>
                    <input v-model="p1number" type="checkbox">
                </div>
                <div class="modal-content-row">
                    <input v-model="value1" placeholder="Value 1" type="text">
                </div>
                <div class="modal-content-row">
                    <input v-model="param2" placeholder="Param 2" type="text">
                    <label>Число</label>
                    <input v-model="p2number" type="checkbox">
                </div>
                <div class="modal-content-row">
                    <input v-model="value2" placeholder="Value 2" type="text">
                </div>
                <button class="modal-button" @click="fetchCountPosts">Найти</button>
            </div>
            <div v-else class="modal-finded">
                Найдено записей: {{count}} <br>
                Страниц: {{pages}}
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import axios from "axios"
export default {
    name: "StartPage",
    data(){
        return {
            isOpen: true,
            table: "",
            sort: "",
            param1: "",
            p1number: false,
            param2: "",
            p2number: false,
            value1: "",
            value2: "",
            proxyFields: [
                "id",
                "fid",
                "os",
                "uptime",
                "host",
                "ip",
                "countryId",
                "regionId",
                "applicationId",
                "city",
                "postalCode",
                "pushId",
                "device",
                "version",
                "isGone",
                "status",
                "lifeCycle",
                "createDate",
                "requestTime"
            ],
            historyFields: [
                "allowedIps",
                "backConnectPort",
                "backConnectServerIp",
                "createDate",
                "currentProxyIp",
                "currentTry",
                "id",
                "lastError",
                "proxyId",
                "serverInternalIp",
                "serverPort",
                "serverPublicIp",
                "status",
                "updateDate",
                "userId",
                "version"
            ],
            reverseSort: false,
            isSearch: false,
            count:0,
            pages: 0,
            currentPage: 1,
            items:[]
        }
    },
    methods: {
        openModal(){
            this.isSearch = false;
            this.isOpen = true;
            this.table = "history"
            this.sort = ""
            this.param1 = ""
            this.p1number = false
            this.param2 = ""
            this.p2number = false
            this.value1 = ""
            this.value2 = ""
            this.reverseSort = false
            this.isSearch = false
        },
        async fetchCountPosts(){
            this.pages = 0
            this.count = 0
            this.items = []
            let value1 = this.p1number ? Number(this.value1) : this.value1;
            let value2 = this.p2number ? Number(this.value2) : this.value2; 
            let sortDir = this.reverseSort ? "desc" : "asc"
            await axios.post("https://us-central1-serv-4e6f4.cloudfunctions.net/search",{
                table: this.table,
                param1: this.param1,
                param2: this.param2,
                value1,
                value2,
                sort: this.sort,
                sortDir
            }).then(async (response) => {
                this.count = response.data.count
                this.pages = Math.ceil(response.data.count / 2)
                this.isSearch = true
                await this.fetchPages()
            })
        },
        async fetchPages(){
            let value1 = this.p1number ? Number(this.value1) : this.value1;
            let value2 = this.p2number ? Number(this.value2) : this.value2; 
            let sortDir = this.reverseSort ? "desc" : "asc"
            await axios.post("https://us-central1-serv-4e6f4.cloudfunctions.net/getPage",{
                table: this.table,
                param1: this.param1,
                param2: this.param2,
                value1,
                value2,
                sort: this.sort,
                sortDir,
                page: this.currentPage,
                count: 2,
            }).then(response => {
                this.items = response.data.value
            })
        },
        async setPage(page){
            this.currentPage = page
            await this.fetchPages()
        }
    }
}
</script>
<style lang="scss">
*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
#app{
    margin: 0!important;
}
.start-page{
    background: rgb(204, 204, 204);
    height: 100vh;
    width: 100%;
    position: relative;
}
.header{
    border-bottom: 1px solid #000;
    padding: 10px 10px;
}
.search-btn{
    display: block;
    padding: 5px 10px;
    background: #fff;
    outline: none;
    border: 1px solid #000;
    border-radius: 5px;
    width: 100px;
    margin-left: auto;
    cursor: pointer;
}
.no-posts{
    margin-top: 200px;
}
.modal{
    background: rgba(0,0,0,0.5);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    &-container{
        width: 300px;
        background: #fff;
        border: 1px solid grey;
        border-radius: 6px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding: 10px;
    }
    &-header{
        display: flex;
        &-title{
            margin: auto;
        }
        &-close{
            background: transparent;
            outline: none;
            border: none;
            margin-right: 5px;
            cursor: pointer;
            font-size: 17px;
            &:hover{
                color: red;
            }
        }
    }
    &-content{
        margin-top: 10px;
        input{
            margin-left: 5px;
            height: 20px;
        }
        select{
            margin-left: 5px;
            height: 20px;
            width: 100%;
        }
        &-row{
            display: flex;
            align-items: center;
            label{
                margin-left: 10px;
            }
            &:not(:first-child){
                margin-top: 10px;
            }
        }
    }
    &-button{
        margin-top: 10px;
        padding: 5px 10px;
        border: 1px solid black;
        border-radius: 3px;
        outline: none;
        cursor: pointer;
        &:hover{
            color: blue;
            opacity: .75;
        }
    }
    &-finded{
        margin-top: 10px;
    }
}
.pages{
    display: flex;
    color: blue;
    justify-content: center;
    width: 100%;
    &-item{
        cursor: pointer;
        &:not(:last-child){
            &::after{
                content: ","
            }
        }
        &:not(:first-child){
            margin-left: 2px;
        }
        &:hover{
            opacity: .75;
        }
    }
}
.posts{
    &-header{
        display: flex;
        border-bottom: 1px solid black;
        border-top: 1px solid black;
        &-item{
            padding: 5px;
            &:not(:last-child){
                border-right: 1px solid black;
            }
        }
    }
    &-content{
        &-item{
            display: flex;
            border-bottom: 1px solid black;
            &-attr{
                padding: 5px;
                &:not(:last-child){
                    border-right: 1px solid black;
                }
            }
        }
    }
}
</style>